<template>
  <div class="content">
  	<header class="head">
			<a class="ico back" href="javascript:window.history.go(-1);"></a>
			<h1 class="y-confirm-order-h1"></h1>
      <span class="text" @click="commitFun"></span>
		</header>
		<div class="main plg">
       <div class="feedwrap">
    			 <textarea placeholder="" maxlength="100" class="feedtext" id="Jtextarea" @input="changeText" v-model="text"></textarea>
           <div class="feednum">{{length}}/100</div>
       </div>
		</div>
  </div>
</template>
<script>
export default {

  data () {
    return {
      text : "",
      length : 0
    }
  },  
  methods: {
    commitFun : function(){     
      if( !this.text ){
        this.$store.commit("alert",{show:true,text:""}); 
        return;
      }
      this.$http.post('/Shop/Setting/feedBack', {text:this.text},{emulateJSON:true}).then(function(response){
        this.text = "";
        this.$store.commit("alert",{show:true,text:response.data.message});                                     
      }); 
    },
    changeText(){
      var Jtextarea = document.getElementById("Jtextarea");
      this.length = Jtextarea.value.length;
    }
  }

}
</script>




