import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const state = {
  loadingJson:{text:'..',show:false},
  confirmJson:{show:false, title:'',text:'',cencelBtn:'',confirmBtn:''},
  alertJson:{show:false, title:'', alertBtn:'', text:''},
  hezhangJson:{show:false,jsondata:{}},
  popstate:false,
  conn: new Object(),
  messageData:{},
  waitFun:0,
  status:200000,
  chating:0,
  share:0,
  isActive:'index',
  is_auth:0,
  demandImgList:[]
}

//  mutation 
const mutations = {
  loading (state,datajson){
  	 	state.loadingJson.text = datajson.text ? datajson.text:'..';
      state.loadingJson.show = datajson.show ? datajson.show:false;
  },confirm(state,datajson){
      state.confirmJson.show = datajson.show ? datajson.show:false;
      state.confirmJson.title = datajson.title ? datajson.title:'';
      state.confirmJson.text = datajson.text ? datajson.text:'';
      state.confirmJson.cencelBtn = datajson.cencelBtn!=undefined ? datajson.cencelBtn:'';
      state.confirmJson.confirmBtn = datajson.confirmBtn!=undefined ? datajson.confirmBtn:'';
      if(datajson.call){
        state.confirmJson.call = datajson.call;
      }else{
        state.confirmJson.call = false;
      }
  },alert(state,datajson){
      state.alertJson.show = datajson.show ? datajson.show:false;
      state.alertJson.title = datajson.title ? datajson.title:'';
      state.alertJson.text = datajson.text ? datajson.text:'';
      state.alertJson.alertBtn = datajson.alertBtn!=undefined ? datajson.alertBtn:'';
      if(datajson.call){
        datajson.call()
      }
  },hezhang(state,datajson){
      state.hezhangJson.show = datajson.show ? datajson.show:false;
      state.hezhangJson.jsondata = datajson.jsondata!=undefined ? datajson.jsondata:{};
  },scrollFun(state,obj){
      var startX = 0, startY = 0;
      function touchSatrtFunc(evt) {
          try
          {
              var touch = evt.touches[0]; //  
              var x = Number(touch.clientX); //X  
              var y = Number(touch.clientY); //Y  
              //  
              startX = x;
              startY = y;

          } catch (e) {
              alert( e.message);
          }
      }
      //touchstart  
      var obj_dom = document.getElementById(obj.dom);
     obj_dom.addEventListener('touchstart', touchSatrtFunc, false);
     obj_dom.addEventListener('touchmove',scrlllfunction,false);
      function scrlllfunction (ev){
        
          var _point = ev.touches[0];
          var _top,_sh,_dh,bottomAdr
          if(obj.auto){
             // window
              if( document.body.scrollTop ){
                _top = document.body.scrollTop;                
              }else{
                _top = document.documentElement.scrollTop;
              }
              _sh = document.body.scrollHeight;
              _dh = window.innerHeight;
          }else{
             // 
             _top = obj_dom.scrollTop;
             _sh = obj_dom.scrollHeight;
             _dh = obj_dom.offsetHeight;

          }
            // 
           bottomAdr = _sh - _dh;
          //
          if(_top >= bottomAdr-10 && _point.clientY < startY){
            console.log(obj.bb)
              if(obj.bottomCall){
                obj.bottomCall();
              }
          }
          // 
          if (_top === 0) {
             if(_sh == _dh){
                 ev.preventDefault();
             }
              // 
              if (_point.clientY > startY) {
                  ev.preventDefault();
              } else {
                  // 
                  // 
                  ev.stopPropagation();
              }
          } else if (_top == bottomAdr) {
              // 
              // 
              if (_point.clientY < startY) {
                  ev.preventDefault();
              } else {
                  // 
                  // 
                  ev.stopPropagation();
              }
          } else if (_top > 0 && _top < bottomAdr) {
              ev.stopPropagation();
          } else {
              ev.preventDefault();
          }
      }
  },imini(state){
        function initMessage(messageData) {
            // var messageData = messageData;
            var messageList = localStorage['messageList'];
            /*********/ 
            var is_end = true;
            if( !messageList ){
              messageList = [];
              var node = {};
              node.id = messageData.from;
              node.total = 1;
              switch( messageData.messageType ){
                case 'txt':
                  node.type = 1;
                  node.content = messageData.data;
                  break;
                case 'img':
                  node.type = 2;
                  node.content = "[]";
                case 'audio':
                  node.type = 3;
                  node.content = "[]";
              };
              // node.content = messageData.data;
              node.time = (messageData.ext.time) ? messageData.ext.time : new Date().getTime();
              messageList.push(node);
            }else{
              var messageList = JSON.parse(messageList);
              for(var j=0; j<messageList.length; j++){
                if( messageData.from == messageList[j].id){
                  //id,total,content
                  ++messageList[j].total;
                  switch( messageData.messageType ){
                    case 'txt':
                    messageList[j].type = 1;
                    messageList[j].content = messageData.data;
                    break;
                  case 'img':
                    messageList[j].type = 2;
                    messageList[j].content = "[]";
                  case 'audio':
                    messageList[j].type = 3;
                    messageList[j].content = "[]";
                  };
                  messageList[j].time = (messageData.ext.time) ? messageData.ext.time : new Date().getTime();
                  is_end = false;
                  break;
                }
              }
              //idjson
              if( is_end ){
                var node = {};
                node.id = messageData.from;
                node.total = 1;
                switch( messageData.messageType ){
                  case 'txt':
                    node.type = 1;
                    node.content = messageData.data;
                  break;
                case 'img':
                    node.type = 2;
                    node.content = "[]";
                case 'audio':
                    node.type = 3;
                    node.content = "[]";
                };
                node.time = (messageData.ext.time) ? messageData.ext.time : new Date().getTime();
                messageList.push(node);
              }
            }
            localStorage['messageList'] = JSON.stringify(messageList);
        }
        state.conn = new WebIM.connection({
          https: WebIM.config.https,
          url: WebIM.config.xmppURL,
          isAutoLogin: WebIM.config.isAutoLogin,
          isMultiLoginSessions: WebIM.config.isMultiLoginSessions
        });
        state.conn.listen({
          onOpened: function ( message ) {          //
            state.conn.setPresence();  //  
            console.log('');
          },  
          onClosed: function ( message ) {console.log('')},         //
          onTextMessage: function ( message ) {  //
              console.log(message);
              message.method = 'receive';
              message.messageType = 'txt';
              state.messageData = message;
              initMessage(message);
          },    
          onEmojiMessage: function ( message ) {},   //
          onPictureMessage: function ( message ) {
              var options = {url: message.url};
              options.onFileDownloadComplete = function () {
                  // 
                  message.method = 'receive';
                  message.messageType = 'img';
                  state.messageData = message;
                  initMessage(message);
                  console.log('Image download complete!');
              };
              options.onFileDownloadError = function () {
                  // 
                  console.log('Image download failed!');
              };
              WebIM.utils.download.call(state.conn, options);       // 
          }, //
          onCmdMessage: function ( message ) {
            //initMessage(message);
          },     //
          onAudioMessage: function ( message ) {
            var options = { url: message.url };
    
            options.onFileDownloadComplete = function ( response ) { 
                //responseblobobjectURLaudiosrc
                var objectURL = WebIM.utils.parseDownloadResponse.call(state.conn, response);
                message.url = objectURL;
                message.method = 'receive';
                message.messageType = 'audio';
                state.messageData = message;
                initMessage(message);
            };  

            options.onFileDownloadError = function () {
                // 
            };  

            //mp3
            options.headers = { 
              'Accept': 'audio/mp3'
            };

            WebIM.utils.download.call(state.conn, options);
          },   //
          onLocationMessage: function ( message ) {},//
          onFileMessage: function ( message ) {},    //
          onVideoMessage: function ( message ) {},   //
          onPresence: function ( message ) {},       //
          onRoster: function ( message ) {},         //
          onInviteMessage: function ( message ) {},  //
          onOnline: function () {},                  //
          onOffline: function () {
            console.log('');
          },                 //
          onError: function ( message ) {}           //
        });
  },imlogin(state,userMsg){
      // 
      var options = { 
        apiUrl: WebIM.config.apiURL,
        user: userMsg.userId,
        pwd: userMsg.passWord,
        appKey: WebIM.config.appkey
      };
      state.conn.open(options);
  },logout(state){
    state.conn.close();
  },sendPrivateText(state,sendJson){
      function saveChat(to, type, content){
        to = to.replace('yjwy', '');
        var content = JSON.stringify({type:type,content:content});
        requestJson = {to_id:to,content:content};
        this.$http.post('/Shop/Chat/saveChat', requestJson, {emulateJSON:true}).then(function(response){}); 
      }
      var id = state.conn.getUniqueId();//id
      var msg = new WebIM.message('txt', id);
      msg.set({
        msg: sendJson.msg,
        to: sendJson.receiveId,    //
        action : 'action',         //cmd
        ext :sendJson.msgArray,    //
        roomType: false,
        chatType: 'singleChat',
        success: function ( id,serverMsgId ) {
            //
            console.log('');
            state.messageData = {
                method : 'send',
                type : 'chat',
                messageType : 'txt',
                data : sendJson.msg,
                ext : sendJson.msgArray,
            };
            saveChat(sendJson.receiveId, 1, sendJson.msg);
            if(sendJson.call){
                sendJson.call()
            }
        }
      }); 
      msg.body.chatType = 'singleChat';
      state.conn.send(msg.body);
  },sendPrivateImg(state,sendJson){
      function saveChat(to, type, content){
        to = to.replace('yjwy', '');
        var content = JSON.stringify({type:type,content:content});
        requestJson = {to_id:to,content:content};
        this.$http.post('/Shop/Chat/saveChat', requestJson, {emulateJSON:true}).then(function(response){}); 
      }
      var id = state.conn.getUniqueId();//id
      var msg = new WebIM.message('img', id);
      var input = document.getElementById('em-widget-img-input');  // input
      var file = WebIM.utils.getFileUrl(input);      // 
      var allowType = {
          'jpg': true,
          'gif': true,
          'png': true,
          'bmp': true
      };
      if (file.filetype.toLowerCase() in allowType) {
          msg.set({
              apiUrl: WebIM.config.apiURL,
              file: file,
              to: sendJson.receiveId,     // 
              roomType: false,
              chatType: 'singleChat',
              action : 'action',          //cmd
              ext : sendJson.msgArray,    //
              onFileUploadError: function () {      // 
                  console.log('onFileUploadError');
              },
              onFileUploadComplete: function () {   // 
                  console.log('onFileUploadComplete');
              },
              success: function ( id,serverMsgId ) {                // 
                  console.log("");
                  state.messageData = {
                      method : 'send',
                      type : 'chat',
                      messageType : 'img',
                      url : msg.value.url,
                      ext : sendJson.msgArray,
                  };
                  saveChat(sendJson.receiveId, 2, msg.value.url);
              },
              flashUpload: WebIM.flashUpload
          });
          state.conn.send(msg.body);
      }
  },


}

//  store
//  store 
export default new Vuex.Store({
  state,
  mutations
})


